webpackJsonp([138295329280052],{

/***/ 795:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"rhostem.github.io","url":"https://rhostem.github.io","author":"rhostem","description":"프론트엔드 웹 개발 기술 블로그"}},"markdownRemark":{"html":"<h2 id=\"정규표현식\"><a href=\"#%EC%A0%95%EA%B7%9C%ED%91%9C%ED%98%84%EC%8B%9D\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>정규표현식</h2>\n<p><a href=\"https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/RegExp\" target=\"_blank\" rel=\"nofollow\">정규표현식</a>은 문자열을 표현할 수 있는 패턴이다. 이메일, 전화번호 등의 유효성을 검증하거나, 문자열에서 원하는 부분을 추출하기 위한 용도로 사용된다. 예를 a로 시작하는 영어 단어의 패턴은 아래와 같다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token regex\">/^a[a-z]*$/i</span></code></pre>\n      </div>\n<p>정규표현식은 자바스크립트 객체이며 <code class=\"language-text\">test</code>, <code class=\"language-text\">exec</code> 등 정규표현식을 활용하기 위한 여러 가지 메소드를 가지고 있다. 객체(Object)를 <code class=\"language-text\">new</code> 키워드 없이 중괄호(<code class=\"language-text\">{}</code>, brace)만으로 선언 가능한 것처럼, 정규표현식도 위에서 제시한 것처럼 슬래쉬(/) 사이에 패턴을 입력하는 것으로 만들 수도 있고 <code class=\"language-text\">new</code> 키워드를 사용해서 만들 수도 있다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> startWithA <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Regexp</span><span class=\"token punctuation\">(</span><span class=\"token string\">'^a[a-z]*$'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'i'</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>정규표현식은 특수한 의미가 있는 기호들을 잘 활용해야 한다. 사실 그게 전부라고도 할 수 있다. 하지만 그런 기호들은 실제 의미와 어떠한 관계도 없어 보이는 것들이 대부분이라 정규표현식을 처음 접하면 그저 이해할 수 없는 암호처럼 보이곤 한다. 계속 사용하면서 익숙해지거나 외우는 수밖에 없는 부분이다.</p>\n<h2 id=\"캡쳐링-그룹capturing-group\"><a href=\"#%EC%BA%A1%EC%B3%90%EB%A7%81-%EA%B7%B8%EB%A3%B9capturing-group\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>캡쳐링 그룹(capturing group)</h2>\n<p>정규표현식에서 캡쳐링 그룹은 괄호로 둘러싼 영역이다. 캡쳐링 그룹이 있으나 없으나 패턴의 유효성을 검증하는 <code class=\"language-text\">test</code> 메소드의 결과는 같다. 하지만 문자열(String) 객체가 가지고 있는 match 메소드의 결과는 달라진다. 예를 들어 ‘github.com’ 사이트가 복수의 서브도메인을 가진다고 할 때, 서브도메인을 포함한 주소를 표현하는 패턴은 다음과 같다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">var</span> url <span class=\"token operator\">=</span> <span class=\"token string\">'https://rhostem.github.com'</span>\n<span class=\"token keyword\">var</span> domain <span class=\"token operator\">=</span> <span class=\"token regex\">/https?:\\/\\/\\w+\\.github.com/</span>\nurl<span class=\"token punctuation\">.</span><span class=\"token function\">search</span><span class=\"token punctuation\">(</span>domain<span class=\"token punctuation\">)</span> <span class=\"token comment\">// 0</span>\nurl<span class=\"token punctuation\">.</span><span class=\"token function\">match</span><span class=\"token punctuation\">(</span>domain<span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"https://rhostem.github.com\", index: 0, ...]</span></code></pre>\n      </div>\n<p>서브도메인 영역을 괄호로 둘러싸서 캡쳐링 그룹을 만들면, <code class=\"language-text\">match</code> 메소드의 결과는 달라진다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">var</span> url <span class=\"token operator\">=</span> <span class=\"token string\">'https://rhostem.github.com'</span>\n<span class=\"token keyword\">var</span> domain <span class=\"token operator\">=</span> <span class=\"token regex\">/https?:\\/\\/(\\w+)\\.github.com/</span>\nurl<span class=\"token punctuation\">.</span><span class=\"token function\">search</span><span class=\"token punctuation\">(</span>domain<span class=\"token punctuation\">)</span> <span class=\"token comment\">// 0</span>\nurl<span class=\"token punctuation\">.</span><span class=\"token function\">match</span><span class=\"token punctuation\">(</span>domain<span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"https://rhostem.github.com\", \"rhostem\", index: 0, ...]</span></code></pre>\n      </div>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/search\" target=\"_blank\" rel=\"nofollow\">search</a> 메소드는 문자열 내부에서 정규표현식이 매칭이 시작되는 지점을 반환한다. 위의 예제에서는 문자열 처음부터 매칭되기 때문에 결과가 <code class=\"language-text\">0</code>이다.</p>\n<p><a href=\"https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/String/match\" target=\"_blank\" rel=\"nofollow\">match</a> 메소드의 결과 배열에서  첫 번째 항목은 전체 매칭 문자열이며, 두 번째부터는 캡쳐링 그룹 안에 있는 문자열이 차례대로 들어간다. 캡쳐링 그룹을 사용하면 패턴 안의 원하는 부분을 보다 편리하게 분리해낼 수 있다.</p>\n<p>그리고 String 객체의 <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/replace\" target=\"_blank\" rel=\"nofollow\">replace</a> 메소드도 정규표현식을 활용하는데, 캡쳐링 그룹을 사용하면 패턴 확인과 함께 원하는 부분만 교체하는 기능을 구현할 수 있다. 위의 정규표현식을 조금 수정해서 서브도메인 영역을 <code class=\"language-text\">www</code>문자열로 바꿔보자.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">'rhostem.github.com'</span><span class=\"token punctuation\">.</span><span class=\"token function\">replace</span><span class=\"token punctuation\">(</span><span class=\"token regex\">/^\\w+(\\.github\\.com)$/</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'www$1'</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// www.github.com</span></code></pre>\n      </div>\n<p>서브도메인 영역을 제외한 부분을 캡쳐링 그룹으로 둘러쌌고, replace 메소드의 두번째 인자에는 대체할 문자열을 패턴을 전달했다. 여기서 <code class=\"language-text\">$1</code>은 첫 번째 캡쳐링 그룹으로 둘러싼 영역애 해당한다. 만약 두 번째 캡쳐링 그룹이 있다면 해당 문자열은 <code class=\"language-text\">$2</code>가 될 것이다. 자세한 사항은 <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/replace#Specifying_a_string_as_a_parameter\" target=\"_blank\" rel=\"nofollow\">링크</a>를 참조하기 바란다.</p>\n<h3 id=\"코드-에디터에서의-정규표현식과-캡쳐링-그룹의-활용\"><a href=\"#%EC%BD%94%EB%93%9C-%EC%97%90%EB%94%94%ED%84%B0%EC%97%90%EC%84%9C%EC%9D%98-%EC%A0%95%EA%B7%9C%ED%91%9C%ED%98%84%EC%8B%9D%EA%B3%BC-%EC%BA%A1%EC%B3%90%EB%A7%81-%EA%B7%B8%EB%A3%B9%EC%9D%98-%ED%99%9C%EC%9A%A9\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>코드 에디터에서의 정규표현식과 캡쳐링 그룹의 활용</h3>\n<p>코드 에디터에서 검색을 할 때 정규표현식을 지원하는 경우가 많다. 그리고 캡쳐링 그룹과 <code class=\"language-text\">$1</code>같은 파라미터 문자열도 지원한다. 이를 사용하면 여러 파일을 한꺼번에 쉽게 수정하는 것이 가능하다.</p>\n<p>예를 들어 <a href=\"https://www.styled-components.com/\" target=\"_blank\" rel=\"nofollow\">styled-components</a> 라이브러리를 사용하고 있는데, v3에서 v4로 업그레이드하려고 할 때 사용 모듈 사용 방법이 달라져서 수정할 사항이 많이 생긴다. 대표적으로 <code class=\"language-text\">extend</code> 메소드의 사용방식이 달라진다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// v3</span>\n<span class=\"token keyword\">const</span> ExtendedComponent <span class=\"token operator\">=</span> Component<span class=\"token punctuation\">.</span>extend<span class=\"token template-string\"><span class=\"token string\">`\n color: green;\n`</span></span>\n\n<span class=\"token comment\">// v4</span>\n<span class=\"token keyword\">const</span> ExtendedComponent <span class=\"token operator\">=</span> <span class=\"token function\">styled</span><span class=\"token punctuation\">(</span>Component<span class=\"token punctuation\">)</span><span class=\"token template-string\"><span class=\"token string\">`\n color: green;\n`</span></span></code></pre>\n      </div>\n<p><code class=\"language-text\">extend</code> 메소드를 많이 사용했다면 하나하나 고치는데 제법 많은 시간이 걸릴 것이다. 이럴 때 정규표현식과 Visual Studio Code의 ‘파일에서 바꾸기’ 기능을 사용하면 한 번에 모두 바꿀 수 있다.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-cb9ea.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1060px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 63.96551724137931%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"vscode-replace-with-regex.png\"\n        title=\"\"\n        src=\"/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-9056e.png\"\n        srcset=\"/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-507eb.png 265w,\n/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-63b28.png 530w,\n/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-9056e.png 1060w,\n/static/vscode-replace-with-regex-37b1cecca0d0f884268c52cdc3298dfe-cb9ea.png 1160w\"\n        sizes=\"(max-width: 1060px) 100vw, 1060px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>이처럼 정규표현식은 에디터에서도 검색과 수정에 유용하게 활용할 수 있다.</p>\n<h2 id=\"논-캡쳐링-그룹non-capturing-group\"><a href=\"#%EB%85%BC-%EC%BA%A1%EC%B3%90%EB%A7%81-%EA%B7%B8%EB%A3%B9non-capturing-group\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>논-캡쳐링 그룹(non-capturing group)</h2>\n<p>non-capturing 그룹은 exec, match, replace 등의 메소드의 결과에는 영향을 미치지 않는다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">'javascript'</span><span class=\"token punctuation\">.</span><span class=\"token function\">replace</span><span class=\"token punctuation\">(</span><span class=\"token regex\">/(java)(script)/</span><span class=\"token punctuation\">,</span> <span class=\"token template-string\"><span class=\"token string\">`$2-$1`</span></span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// script-java</span></code></pre>\n      </div>\n<p><code class=\"language-text\">java</code>와 <code class=\"language-text\">script</code>를 캡쳐링 그룹으로 지정했기에 패턴을 사용해서 위치를 바꿀 수 있었다. 하지만 캡쳐링 그룹의 시작 부분에 <code class=\"language-text\">?:</code>을 추가하면 non-capturing 그룹이 되어 매칭 결과에 포함되지 않는다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">'javascript'</span><span class=\"token punctuation\">.</span><span class=\"token function\">replace</span><span class=\"token punctuation\">(</span><span class=\"token regex\">/(java)(?:script)/</span><span class=\"token punctuation\">,</span> <span class=\"token template-string\"><span class=\"token string\">`$2-$1`</span></span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// \"$2-java\"</span></code></pre>\n      </div>\n<p>그럼 이런 생각이 들 것이다. 저건 결국 괄호로 둘러싸지 않은 것과 같은데, 왜 굳이 non-capturing 그룹이라는 패턴을 지원할까? 라고 말이다. non-capturing 문법이 있는 이유는 그룹이 다른 역할로도 사용되기 때문이다. 예를 들어 앞서 서브도메인을 추출한 예제에서, http 프로토콜이 포함된 것과 포함되지 않은 문자열을 모두 매칭시키면서 서브도메인만 캡쳐링 그룹에 포함하고 싶을 때가 있을 것이다. 그러면 아래와 같이 작성할 수 있다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token regex\">/(?:https?:\\/\\/)?(\\w+)\\.github.com/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'rhostem.github.com'</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// [\"rhostem.github.com\", \"rhostem\", index: 0, ...]</span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token regex\">/(?:https?:\\/\\/)?(\\w+)\\.github.com/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'https://rhostem.github.com'</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// [\"https://rhostem.github.com\", \"rhostem\", index: 0, ...]</span></code></pre>\n      </div>\n<p>http 프로토콜 패턴 부분을 그룹으로 만들고, 바로 다음에 조건부 매칭을 의미하는 <code class=\"language-text\">?</code> 기호를 붙여서 프로토콜에 해당하는 문자열 전체가 조건부 패턴이 되었다. 하지만 매칭 결과 배열에는 포함하고 싶지 않기 때문에 <code class=\"language-text\">?:</code> 기호를 사용해서 non-capturing 그룹으로 지정했다. non-capturing 그룹을 사용하지 않았다면 아래처럼 프로토콜 영역도 결과에 포함된다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token punctuation\">[</span><span class=\"token string\">\"https://rhostem.github.com\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"https://\"</span><span class=\"token punctuation\">,</span> <span class=\"token string\">\"rhostem\"</span><span class=\"token punctuation\">,</span> index<span class=\"token punctuation\">:</span> <span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span><span class=\"token punctuation\">]</span></code></pre>\n      </div>\n<p>non-capturing 그룹 덕분에 항상 결과 배열의 2번째 아이템이 서브도메인이 될 것이므로, 다음과 같은 함수를 작성해서 서브도메인을 추출할 수 있게 된다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> getSubDomain <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>url <span class=\"token operator\">=</span> <span class=\"token string\">''</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> matches <span class=\"token operator\">=</span> <span class=\"token regex\">/(?:https?:\\/\\/)?(\\w+)\\.github.com/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span>url<span class=\"token punctuation\">)</span>\n  <span class=\"token keyword\">return</span> matches <span class=\"token operator\">&amp;&amp;</span> matches<span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">]</span> <span class=\"token comment\">// 첫 번째 캡쳐링 그룹</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h2 id=\"lookahead\"><a href=\"#lookahead\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Lookahead</h2>\n<p>Lookahead도 그룹 패턴으로, Lookahead 그룹 안에 있는 패턴이 <strong>뒤에</strong> 올 때만 매칭이 된다. 이름이 붙은 이유는 ‘그룹의 시점에서 앞쪽에 있는 문자열을 확인한다’는 의미로 받아들이면 될 것 같다.</p>\n<p>캡쳐링 그룹과 다른 점은 <strong>전체 매칭 결과에 Lookahead 그룹 안에 있는 패턴이 포함되지 않는다</strong>는 것이다. lookahead(앞쪽을 보다)라는 이름이 가지는 의미 그대로 관찰자 역할을 할 뿐, 전체 패턴에서는 완전히 제외되어 있다고 여기면 될 것 같다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// capturing group</span>\n<span class=\"token operator\">/</span><span class=\"token function\">java</span><span class=\"token punctuation\">(</span>script<span class=\"token punctuation\">)</span><span class=\"token operator\">/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'javascript'</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"javascript\", \"script\", index: 0, ...]</span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// positive lookahead</span>\n<span class=\"token operator\">/</span><span class=\"token function\">java</span><span class=\"token punctuation\">(</span><span class=\"token operator\">?</span><span class=\"token operator\">=</span>script<span class=\"token punctuation\">)</span><span class=\"token operator\">/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'javascript'</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"java\", index: 0, ...]</span></code></pre>\n      </div>\n<p>Lookahead에는 positive와 negative가 있는데, 앞서 제시한 <code class=\"language-text\">?=</code> 기호는 positive lookahead로서 그룹 안의 패턴이 뒤따라야 함을 의미하며, nagative lookahead를 나타내는 <code class=\"language-text\">?!</code> 기호는 그룹 안의 패턴이 뒤따르지 말아야 함을 의미한다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token regex\">/java(?!script)/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'java coffee'</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"java\", index: 0, ... ]</span></code></pre>\n      </div>\n<p>이 패턴을 사용하면 ‘java’ 뒤에 ‘script’라는 문자열이 오지 않는 모든 문자열에 매칭된다.</p>\n<h2 id=\"lookbehind\"><a href=\"#lookbehind\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Lookbehind</h2>\n<p>Lookahead와 정반대의 역할을 하며 그룹 안의 패턴이 <strong>앞에</strong> 나올 때 매칭이 된다. 하지만 ECMAScript 2018 스펙이라서 글을 쓰는 시점에서는 Safari, FireFox는 브라우저는 지원하지 않으며 Chrome 브라우저에서만 사용 가능하다.</p>\n<p>Lookbehind도 positive, negative가 있으며, positive lookbehind는 <code class=\"language-text\">?&lt;=</code>, negative lookbehind는 <code class=\"language-text\">?&lt;!</code> 기호로 표현한다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// java 다음에 script가 와야 한다.</span>\n<span class=\"token operator\">/</span><span class=\"token punctuation\">(</span><span class=\"token operator\">?</span><span class=\"token operator\">&lt;=</span>java<span class=\"token punctuation\">)</span>script<span class=\"token operator\">/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'javascript'</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"script\", index: 6, ...]</span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// java가 아닌 문자열 다음에 script가 와야 한다.</span>\n<span class=\"token operator\">/</span><span class=\"token punctuation\">(</span><span class=\"token operator\">?</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">!</span>java<span class=\"token punctuation\">)</span>script<span class=\"token operator\">/</span><span class=\"token punctuation\">.</span><span class=\"token function\">exec</span><span class=\"token punctuation\">(</span><span class=\"token string\">'purescript'</span><span class=\"token punctuation\">)</span> <span class=\"token comment\">// [\"script\", index: 4, ...]</span></code></pre>\n      </div>","timeToRead":6,"fields":{"tagSlugs":["/tag/javascript","/tag/정규표현식"]},"frontmatter":{"title":"자바스크립트 정규표현식의 capturing group","subTitle":"capturing group, non-capturing group, 그리고 lookahead","tags":["JavaScript","정규표현식"],"date":"2018-11-11","mainImageAlt":"","description":"","mainImage":{"childImageSharp":{"sizes":{"aspectRatio":2.1333333333333333,"base64":"data:image/jpeg;base64,","sizes":"(max-width: 1280px) 100vw, 1280px","src":"/static/main-f0f437cc7c9297f33739560fbc9e4e00-6ff54.jpg","srcSet":"/static/main-f0f437cc7c9297f33739560fbc9e4e00-3c4d0.jpg 320w,\n/static/main-f0f437cc7c9297f33739560fbc9e4e00-e154f.jpg 640w,\n/static/main-f0f437cc7c9297f33739560fbc9e4e00-6ff54.jpg 1280w"}}}}}},"pathContext":{"slug":"/posts/2018-11-11-regex-capture-group/"}}

/***/ })

});


